<template>
    <div class="configuration">
        <div class="m-t-10 ">
            <p class="header-line">
                <i class="fa fa-cogs m-r-10" aria-hidden="true"></i>
                {{$t('configuration.configurations')}}
            </p>
        </div>
        <el-row>
            <el-col :lg="10" :md="21" :offset="1">
                <div class="box-card">
                    <div class="cf card-header">
                        <i class="fa fa-th-large m-r-10" aria-hidden="true"></i>
                        {{$t('configuration.menu')}} {{$t('configuration.configuration')}}
                        <el-button class="fr m-r-5" type="primary" size="mini" @click="setServerMenu()">
                            {{$t('global.save')}}
                        </el-button>
                    </div>
                    <div class="card-body">
                        <el-form>
                            <el-form-item :label="$t('label.deviceOs')" :label-width="formLabelWidth">
                                <el-checkbox-group v-model="osType">
                                    <el-checkbox label="android" @change="checked=>selectOs(checked, groupType.android)">Android</el-checkbox>
                                    <el-checkbox label="linux" @change="checked=>selectOs(checked, groupType.linux)">Linux</el-checkbox>
                                    <el-checkbox label="windows" @change="checked=>selectOs(checked, groupType.windows)">Windows</el-checkbox>
                                </el-checkbox-group>
                            </el-form-item>
                            <el-form-item :label="$t('label.functionMenu')" :label-width="formLabelWidth">
                                <el-checkbox-group v-model="menu">
                                    <el-checkbox 
                                    v-for="item in functionMenu" 
                                    :label="item.value" 
                                    :key="item.value" v-show="menuIsShow(item.os)">
                                    {{$t(item.label)}}
                                    </el-checkbox>
                                </el-checkbox-group>
                            </el-form-item>
                        </el-form>
                    </div>
                </div>
            </el-col>

            <el-col :lg="10" :md="21" :offset="1">
                <div class="box-card">
                    <div class="cf card-header">
                        <i class="fa fa-download m-r-10" aria-hidden="true"></i>
                        {{$t('configuration.degeOnDeployAppDownload')}}
                        <!-- <i class="fa fa-download fr m-r-5 c-primary pointer" aria-hidden="true" @click="configurableApkQRcodeVisible=true"></i> -->
                    </div>
                    <div class="card-body text-center">
                        <qr-code :value="downloadQrCodeValue" :options="{ width: 260}"　class="block-center"></qr-code>
                        <ul>
                            <li class="introduce">
                                {{$t('configuration.scanQRCode')}}
                            </li>
                            <li class="introduce">
                                {{$t('configuration.scanQRCode1')}}
                            </li>
                            <li class="introduce">
                                <i class="fa fa-hand-o-right m-r-5" aria-hidden="true"></i>
                                <span @click="downloadDeployApp()" class="can-click">
                                    {{$t('global.download')}}
                                </span>
                            </li>
                        </ul>
                    </div>
                </div>
            </el-col>
            
        </el-row>
        <el-row>
            
            <el-col :lg="10" :md="21" :offset="1">
                <div class="box-card">
                    <div class="cf card-header">
                        <i class="fa fa-download m-r-10" aria-hidden="true"></i>
                        {{$t('configuration.agentDownload')}}
                        <!-- <i class="fa fa-download fr m-r-5 c-primary pointer" aria-hidden="true" @click="configurableApkQRcodeVisible=true"></i> -->
                    </div>
                    <div class="card-body">
                        <div class="agentInfo">
                            <p>
                                <span class="agentlable">{{$t('configuration.baiduyun')}}</span> <a :href="address" target="_blank" class="agentVal">{{address}}</a>
                                
                            </p>
                            <p>
                                <span class="agentlable">{{$t('configuration.fetchCode')}}</span> <span class="agentVal">{{fetchcode}}</span>
                            </p>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>

import {getServerConfigApi, setServerConfigApi, getRepoServerApi, getQRcodeDataApi, setServerMenuApi, getServerMenuApi} from "../restfulapi/serverConfigApi"
import handleResponse from '../restfulapi/handleResponse';
import {setSession, getSession, checkSession} from "../../assets/js/storage"
import {applicationTitle, downloadBaseUrl, fetchcode, address, functionMenu, groupType} from "../../assets/js/constant"
import qrCode from '@chenfengyuan/vue-qrcode';
import {deleteAllApi} from "../restfulapi/aStoreApi"
import {mapState} from 'vuex'

export default {
    
    data(){
        let menuArray = [];
        functionMenu.forEach((item) => {
            menuArray.push(item.value)
        })
        return{
            formLabelWidth: "10rem",
            username: "",
            blobForm: {
                bloburl: "",
                bucket: "",
                accesskey: "",
                secretkey: ""
            },
            rules: {
                bloburl: {required: true , trigger: 'blur'},
                bucket: {required: true , trigger: 'blur'},
                accesskey: {required: true , trigger: 'blur'},
                secretkey: {required: true , trigger: 'blur'},
            },
            isLoading: false,
            downloadQrCodeValue: "http://106.12.15.5:30002/DEPLOYREPO/EI-Link.apk",
            downloadOptions:[
                {name: "Rk3288", value: "rk3288"},
                {name:"Rk3399", value: "rk3399"}, 
                {name: "Baytrail", value: "baytrail"}, 
                {name: "Imx6", value: "imx6"}, 
                {name: "Apollolake", value: "apollolake"}, 
                {name: "Aim", value: "aim"}
            ],
            address: address,
            fetchcode: fetchcode,
            functionMenu,
            osType: [groupType.android, groupType.linux, groupType.windows],
            menu: menuArray,
            groupType,
        }

    },

    components: {
        qrCode
    },

    methods: {
        menuIsShow(os){
            return _g.matchInArray(this.osType, os)||os == 'mix'
        },
        
        selectOs(value, cid){
            if(value){
                this.menu.forEach(menuItem => {
                    functionMenu.forEach(menuObj => {
                        if(menuObj.os.indexOf(cid)>-1){
                            if(this.menu.indexOf(menuObj.value) == -1){
                                this.menu.push(menuObj.value)
                            }
                        }
                        
                    })
                })
            }
        },
        saveServerAddress(cid){
            let serverForm;
            switch(cid){
                case "blob":
                serverForm = "blobForm";
                break;
                default:
                serverForm = "";
            }
            this.$refs[serverForm].validate((valid) => {
                if (valid) {
                    this.$swal({
                        title: "Note!",
                        text: this.$t('configuration.noteContent'),
                        icon: "warning",
                        buttons: true,  
                        dangerMode: true,
                    }).then((result) => {
                        if(result){
                            this.isLoading = true;
                            setServerConfigApi(this[serverForm]).then((res) => {
                                this.isLoading = false;
                                handleResponse(res, (data) => {
                                    if(data.status === "CHANGED"){
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            deleteAllApi();
                                            this.getConfiguration();
                                        })
                                    }else{
                                        _g.handleError(data)
                                    }
                                })
                            })
                        }
                    }) 
                    
                }
            })
        },
        setServerMenu(){
            if(this.osType.length == 0){
                this.$swal("",this.$t('configuration.noOs'),"info", {button: this.$t('global.confirm')})
                return;
            }
            let menu = [];
            this.menu.forEach(menuItem => {
                let res = false;
                functionMenu.forEach(menuObj => {
                    
                    if(menuObj.value == menuItem){
                        this.osType.forEach((osItem) => {
                            if(menuObj.os.indexOf(osItem)>-1|| menuObj.os == groupType.mix){
                                res = true;
                            }
                        })
                    }
                })
                
                if(res){
                    menu.push(menuItem);
                }
            })
            // console.log(menu);
            setServerMenuApi(this.username, this.osType, menu).then((res) => {
                handleResponse(res, (data) => {
                    if(data.status === "CHANGED"){
                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                            this.getConfiguration();
                        })
                    }else{
                        _g.handleError(res)
                    }
                })
            })
        },
        getConfiguration(){
            if(checkSession("username")){
                this.username = getSession("username");
                getServerConfigApi(this.username).then((res) => {
                    handleResponse(res, (data) => {
                        this.blobForm.bloburl = data.bloburl;
                        this.blobForm.bucket = data.bucket;
                        this.blobForm.accesskey = data.accesskey;
                        this.blobForm.secretkey = data.secretkey;
                    })
                })
                getServerMenuApi(this.username).then((res) => {
                    handleResponse(res, (data) => {
                        if(data.menu){
                            this.menu = data.menu;
                            this.osType = data.ostype;
                            let count = this.menu.length;
                            _g.menuStatusCount(count);
                            _g.osStatusCount(this.osType.length);
                            setSession("apphub_menu", this.menu);
                            setSession("apphub_os", this.osType);
                        }
                    })
                })
            }else{
                this.$swal("", this.$t('global.loginExpired'), "info", {button: this.$t('global.confirm')}).then(() => {
                    this.$router.replace("/");
                    return;
                })    
            }

        },
 	getQRcodeData(){
            getQRcodeDataApi().then((data) => {
                handleResponse((data), (res) => {
                    if(_g.isObject(res)){
                        console.log("qrcodedata:"+JSON.stringify(res));
                        this.configQrCodeValue = JSON.stringify(res);
                        this.isShowConfigQrCode = true;
                    }
                })
            })
        },

        downloadDeployApp(){
            window.location.href= this.downloadQrCodeValue;
        },  

	downloadConfigFile(){
            window.location.href="/api/configmgr/downloadConfigFile";
        },
        downloadAgent(keywords){
            window.location.href=downloadBaseUrl+"/downloadApp?pkgname=com.adv.client&keywords="+keywords;
        }
   
    },
    watch: {
        menuStatusCount(){
            this.getConfiguration()
        },

        osStatusCount(){
            this.getConfiguration()
        }
    },
    computed:{
        ...mapState({
            menuStatusCount: "menuStatusCount",
            osStatusCount: "osStatusCount",
        }), 
    },
    created(){
        this.getConfiguration();
    },
}
</script>

<style lang="scss">
@import "../../assets/css/colors";
    .configuration{
        .box-card {
            margin:2rem 0;
            .card-header{
                padding: 18px 20px;
                border-bottom: 1px solid #ebeef5;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
            }
            .card-body {
                padding: 20px;
                margin-bottom: 1rem;
                input{
                    min-width: 180px;
                    width: 95%;
                }
                .introduce {
                    padding: 5px;
                    color: #606266;
                    font-size:14px;
                }
                .can-click{
                    color: $primary-color;
                    cursor: pointer;
                    text-decoration: underline;
                }
            }
        }
    }
    .cnote{
        margin-left: 30px;
        margin-bottom: 5px;
        color: $darkgray-color;
        font-size:14px;
        i{
            color: $danger-color;
            margin-right: 8px;
            
        }
    }

    .agentInfo{

        .agentlable{
            display: inline-block;
            width: 210px;
            text-align: right;
            font-size: 15px;
            color: #606266;
            line-height: 40px;
            font-weight: 600;
        }
        .agentVal{
            margin-left: 10px;
            color: $primary-color;
        }
        a{
            text-decoration: underline;;
        }
    }
</style>



