import {repoApiDelete} from '@/assets/js/baseApi'
import {getRepoServerApi} from "../restfulapi/serverConfigApi"
import {getToken} from "./getRepoTokenApi"

let deleteAllApi = function(){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getRepoAppUrl =  repoUrl + "/user/deleteAll";
            repoApiDelete(getRepoAppUrl).then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
    
    })
}

export {
    deleteAllApi
}


